.task {
    width: 248px;
    padding: 8px;
    padding-left: 12px;

    cursor: pointer;

    color: #000;
    border-radius: 3px;
    background-color: #fff;
    box-shadow: 0px 1px 4px rgba(9, 45, 66, 0.25);

    font-size: 1rem;
    line-height: 1.5em;
}

