@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&subset=cyrillic);
.task {
    width: 248px;
    padding: 8px;
    padding-left: 12px;

    cursor: pointer;

    color: #000;
    border-radius: 3px;
    background-color: #fff;
    box-shadow: 0px 1px 4px rgba(9, 45, 66, 0.25);

    font-size: 1rem;
    line-height: 1.5em;
}

/* .task--shadow {
    width: 248px;
    margin-bottom: 8px;

    border-radius: 3px;
    background-color: #c9d2d9;
} */

.button {
    padding: 6px 8px;

    cursor: pointer;

    color: #fff;
    border: 0;
    border-radius: 3px;
    outline: none;
    background-color: #39c071;

    font-size: 1rem;
    font-weight: bold;
    line-height: 1.5em;
}

.button:hover {
    background-color: #36b46b;
}

.Form {
    padding: 1px 12px 12px 12px
}

.Form-newColumn {
    padding-top: 12px
}

.Form__textarea {
    overflow: hidden;

    width: 100%;
    margin-bottom: 8px;
    padding: 8px;
    padding-left: 12px;

    resize: none;

    color: #000;
    border: 0;
    border-radius: 3px;
    outline: none;
    background-color: #fff;
    box-shadow: 0px 1px 4px rgba(9, 45, 66, 0.25);

    font-family: inherit;
    font-size: 1rem;
    line-height: 1.5em;
}

.Form__cancel {
    position: relative;

    float: right;

    margin-top: 9px;

    cursor: pointer;
}


.column__columnButton {
    padding: 12px;

    cursor: pointer;

    color: #6b808c;
    border-radius: 3px;
}

.column__columnButton:hover {
    color: #091e42;
    background-color: #d1d7db;
}

.column {
    display: flex;
    flex: 0 0 272px;
    flex-direction: column;

    max-width: 272px;
    min-height: 100%;
    max-height: calc(100vh - 40px);
    margin-right: 12px;

    border-radius: 3px;
    background-color: #dfe3e6;
}

.column:first-child {
    margin-left: 20px;
}

.column:last-child {
    margin-right: 0;
}

.column__title {
    overflow: hidden;

    margin: 0;
    padding: 8px 12px 12px;

    text-overflow: ellipsis;

    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    background-color: #dfe3e6;

    font-size: 1rem;
    font-weight: bold;
    line-height: 1.5em;
}

.column__taskList {
    overflow-y: scroll;
    flex: 1 1;
}

.taskList {
    min-height: 12px;
    padding-right: 12px;
    padding-left: 12px;
}

.taskList__task {
    margin: 0;
    margin-bottom: 8px;
}


*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    font-size: 13px;
    line-height: 1.5em;
}

body {
    margin: 0;

    font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
    height: 100vh;

    background: url(/static/media/background.ae9cc6f5.jpg);
}

.app {
    display: flex;
    overflow-y: scroll;
    align-items: flex-start;
    flex-wrap: nowrap;

    padding: 20px 0;
}

.app::after {
    flex: 0 0 20px;

    height: 1px;

    content: "";
}

