.column {
    display: flex;
    flex: 0 0 272px;
    flex-direction: column;

    max-width: 272px;
    min-height: 100%;
    max-height: calc(100vh - 40px);
    margin-right: 12px;

    border-radius: 3px;
    background-color: #dfe3e6;
}

.column:first-child {
    margin-left: 20px;
}

.column:last-child {
    margin-right: 0;
}

.column__title {
    overflow: hidden;

    margin: 0;
    padding: 8px 12px 12px;

    text-overflow: ellipsis;

    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    background-color: #dfe3e6;

    font-size: 1rem;
    font-weight: bold;
    line-height: 1.5em;
}

.column__taskList {
    overflow-y: scroll;
    flex: 1;
}

.taskList {
    min-height: 12px;
    padding-right: 12px;
    padding-left: 12px;
}

.taskList__task {
    margin: 0;
    margin-bottom: 8px;
}

