.Form {
    padding: 1px 12px 12px 12px
}

.Form-newColumn {
    padding-top: 12px
}

.Form__textarea {
    overflow: hidden;

    width: 100%;
    margin-bottom: 8px;
    padding: 8px;
    padding-left: 12px;

    resize: none;

    color: #000;
    border: 0;
    border-radius: 3px;
    outline: none;
    background-color: #fff;
    box-shadow: 0px 1px 4px rgba(9, 45, 66, 0.25);

    font-family: inherit;
    font-size: 1rem;
    line-height: 1.5em;
}

.Form__cancel {
    position: relative;

    float: right;

    margin-top: 9px;

    cursor: pointer;
}

